.loader-accent {
  background-color: #03B092;
  color: #03B092; }

.page-loader, .dialog-loader {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px; }

.page-loader {
  z-index: 1022; }

.dialog-loader {
  z-index: 1024; }

.nested-loader {
  z-index: 1024; }

.nested-loader .loader {
  background-color: transparent; }

.page-loader .loader, .dialog-loader .loader {
  background-color: rgba(0, 0, 0, 0.6); }

.loader {
  align-content: center;
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: center;
  position: relative;
  width: 100%;
  z-index: 1023; }
  .loader .container {
    position: relative;
    width: 50px;
    height: 50px;
    opacity: 1; }
    .loader .container .circle {
      position: absolute;
      width: 44px;
      height: 44px;
      opacity: 0;
      transform: rotate(225deg);
      animation: orbit 4.0s infinite; }
      .loader .container .circle .inner-circle {
        position: absolute;
        width: 6px;
        height: 6px;
        opacity: 1;
        border-radius: 50%; }
    .loader .container .circle-1 {
      animation-delay: 0 !important; }
    .loader .container .circle-2 {
      animation-delay: 0.2s !important; }
    .loader .container .circle-3 {
      animation-delay: 0.4s !important; }
    .loader .container .circle-4 {
      animation-delay: 0.6s !important; }
    .loader .container .circle-5 {
      animation-delay: 0.8s !important; }

@keyframes orbit {
  0% {
    opacity: 1;
    z-index: 99;
    transform: rotate(180deg);
    animation-timing-function: linear; }
  7% {
    opacity: 1;
    transform: rotate(300deg);
    animation-timing-function: linear; }
  35% {
    opacity: 1;
    transform: rotate(450deg);
    animation-timing-function: linear; }
  45% {
    opacity: 1;
    transform: rotate(660deg);
    animation-timing-function: linear; }
  70% {
    opacity: 1;
    transform: rotate(810deg);
    animation-timing-function: linear; }
  75% {
    opacity: 1;
    transform: rotate(900deg);
    animation-timing-function: linear; }
  76% {
    opacity: 0;
    transform: rotate(900deg);
    animation-timing-function: ease-out; }
  100% {
    opacity: 0;
    transform: rotate(900deg); } }
